.navbar {
  display: flex;
  justify-content: space-between;
  border-radius: 40%;
  padding-bottom: 0.5%;
  padding-top: 0.5%;
  width: 98%;
}

.nav-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: antiquewhite;
  border-radius: 50%;
  padding: 0.5%;
}

.nav-buttons:active {
  transform: scale(0.9);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.4);
  background-color: #2980b9;
}
